.modal[data-v-9323fb46] {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  background: rgba(0,0,0,0.6);
  z-index: 5000;
}
.spinner[data-v-9323fb46] {
  width: 40px;
  height: 40px;

  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate( -50%, -50%);
  transform: translate( -50%, -50%);
}
.double-bounce1[data-v-9323fb46], .double-bounce2[data-v-9323fb46] {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background-color: #ffffff;
  opacity: 0.6;
  position: absolute;
  top: 0;
  left: 0;

  -webkit-animation: sk-bounce-data-v-9323fb46 2.0s infinite ease-in-out;
  animation: sk-bounce-data-v-9323fb46 2.0s infinite ease-in-out;
}
.double-bounce2[data-v-9323fb46] {
  -webkit-animation-delay: -1.0s;
  animation-delay: -1.0s;
}
@-webkit-keyframes sk-bounce-data-v-9323fb46 {
0%, 100% { -webkit-transform: scale(0.0)
}
50% { -webkit-transform: scale(1.0)
}
}
@keyframes sk-bounce-data-v-9323fb46 {
0%, 100% {
    transform: scale(0.0);
    -webkit-transform: scale(0.0);
}
50% {
      transform: scale(1.0);
      -webkit-transform: scale(1.0);
}
}


